<script setup>
import { ref, onBeforeMount } from "vue";
import Auth from "./api/Auth";

const drawer = ref(true)

const user = ref()


async function logout() {
  if (confirm('Keluar sesi ini?')) {
    await Auth.logout()
    location.reload()
  }
}

onBeforeMount(async () => {
  const res = await Auth.find().catch((err) => err)
  user.value = res.username
})

</script>

